import React, { useState, useEffect} from "react";
import { Link, useNavigate } from "react-router-dom";
import { Component1174 } from "../../components/Component1174";
import { Menu } from "../../components/Menu";
import { StateOffWrapper } from "../../components/StateOffWrapper";
import { Search } from "../../components/Search";
import "./style.css";
import AsyncSelect from 'react-select/async';
import Papa from 'papaparse';

export const Home = () => {
  const navigate = useNavigate();  

  const [stocksData, setStocksData] = useState([]);
  const [inputValue, setInputValue] = useState("");

  useEffect(() => {
    Papa.parse("/symbol_data/korea_stock_symbols.csv", {
      download: true,
      header: true,
      delimiter: ",",
      skipEmptyLines: true,
      complete: function(results) {
        if (results.errors.length > 0) {
          // console.error("CSV   :", results.errors);
          return;
        }
        
        // console.log("CSV   :", results.data); //   
        const validData = results.data.filter((row) => row.label && row.value);
        // console.log(" :", validData); //   
        const formattedData = validData.map((row) => ({
          label: row.label,
          value: row.value,
        }));
        setStocksData(formattedData);
      },
    });
  }, []);

  const filterOptions = (inputValue) => {
    return stocksData.filter(
      (i) => i.label && i.label.toLowerCase().includes(inputValue.toLowerCase())
    );
  };

  const handleChange = (selectedOption) => {
    if (selectedOption) {
      navigate(`/result-1/${selectedOption.value}`, {
        state: { stockLabel: selectedOption.label },
      });
    }
  };

  const loadOptions = (inputValue, callback) => {
    setTimeout(() => {
      callback(filterOptions(inputValue));
    }, 500);
  };

  const formatOptionLabel = ({ label }, { inputValue }) => {
    const parts = label.split(new RegExp(`(${inputValue})`, 'i'));
    return (
      <div>
        {parts.map((part, index) =>
          part.toLowerCase() === inputValue.toLowerCase() ? (
            <span key={index} style={{ color: '#7d49f5' }}>{part}</span>
          ) : (
            part
          )
        )}
      </div>
    );
  };

  const customStyles = {
    placeholder: (provided) => ({
      ...provided,
      fontFamily: "Noto Sans KR, Helvetica",
      paddingLeft: '10px', //    
    }),
    input: (provided) => ({
      ...provided,
      fontFamily: "Noto Sans KR, Helvetica",
      paddingLeft: '10px', //    
    }),
    control: (provided, { isFocused }) => ({
      ...provided,
      fontFamily: "Noto Sans KR, Helvetica",
      minHeight: '45px', //   
      borderColor: isFocused ? '#7d49f5' : provided.borderColor, //     
      boxShadow: isFocused ? '0 0 0 1px #7d49f5' : 'none',
    //       
      '&:hover': {
        borderColor: '#7d49f5', //     
      },
     
    }),
    dropdownIndicator: (provided, { isFocused }) => ({
      ...provided,
      color: isFocused ? '#7d49f5':provided.color,  //    
    }),

    noOptionsMessage: (provided) => ({
      ...provided,
      fontFamily: "Noto Sans KR, Helvetica"
    }),

    loadingMessage: (provided) => ({
      ...provided,
      fontFamily: "Noto Sans KR, Helvetica"
    }),

    option: (provided, { isFocused, isSelected }) => {
      return {
        ...provided,
        backgroundColor: isSelected ? '#F2ECFF' : isFocused ? '#F2ECFF' : undefined,
        //      
        fontFamily: "Noto Sans KR, Helvetica",
        paddingLeft: '20px',
      };
    },
  };

  const handleStockClick = (symbol, label) => {
    navigate(`/result/${symbol}`, { state: { stockLabel: label } });
  };


  return (
    <div className="home">
      <div className="frame-8">
        <div className="content-4">
          <div className="stock-frame">
            <div className="stocks">
            <div onClick={() => handleStockClick('KRX:035420', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-7"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                spanClassName="stock-6"
                state="off"
              />
            </div>
            <div onClick={() => handleStockClick('KRX:068270', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-10"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-9"
                spanClassName="stock-6"
                state="off"
                text=""
              />
            </div>
            <div onClick={() => handleStockClick('KRX:005930', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-10"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-11"
                spanClassName="stock-6"
                state="off"
                text=""
              />
            </div>
            <div onClick={() => handleStockClick('KRX:035720', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-7"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-12"
                spanClassName="stock-6"
                state="off"
                text=""
              />
            </div>
            <div onClick={() => handleStockClick('KRX:086520', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-10"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-13"
                spanClassName="stock-6"
                state="off"
                text=""
              />
            </div>
            <div onClick={() => handleStockClick('KRX:005380', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-7"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-14"
                spanClassName="stock-6"
                state="off"
                text=""
              />
            </div>
            
            <div onClick={() => handleStockClick('KRX:373220', 'LG')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-30"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-15"
                spanClassName="stock-6"
                state="off"
                text="LG"
              />
            </div>

            <div onClick={() => handleStockClick('KRX:000660', 'SK')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-16"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-17"
                spanClassName="stock-6"
                state="off"
                text="SK"
              />
            </div>
            <div onClick={() => handleStockClick('KRX:024110', '')}>
              <Component1174
                divClassName="component-1174-instance"
                divClassName1="stock-10"
                divClassName2="stock-8"
                divClassNameOverride="stock-5"
                logoClassName="stock-18"
                spanClassName="stock-6"
                state="off"
                text=""
              />
            </div>
            </div>
          </div>
          <div className="text-container">
            <div className="text-6">
              <div className="text-wrapper-26">  ?</div>
            </div>
            <div className="text-7">
              <p className="text-wrapper-27">    .</p>
            </div>
          </div>
          <Link className="container-wrapper" to="/search">
            <div className="container-3">
            <AsyncSelect
                styles={customStyles}
                cacheOptions
                loadOptions={loadOptions}
                onInputChange={(value) => {
                  setInputValue(value);
                  return value;
                }}
                onChange={handleChange}
                placeholder="  "
                formatOptionLabel={formatOptionLabel}
            />
            </div>
          </Link>
          <div className="menu-wrapper">
            <Menu
              className="menu-2"
              iconVariantsIconHome="/img/home-5.svg"
              iconVariantsIconUnion="/img/union-9.svg"
              iconVariantsState="off"
              iconVariantsState1="on"
              to1="/favorite"
              to2="/profile"
              to3="/search"
            />
          </div>
          <div className="head-2">
            <div className="text-wrapper-29">Home</div>
          </div>
        </div>
      </div>
    </div>
  );
};
